const getElementsByCLASSNAME  = 'getElementsByClassName';
const parentNode = 'parentNode';

export class Pages {
    private body: HTMLBodyElement;

    constructor() {
        this.body = document.getElementsByTagName('body')[0];
    }

    public init() {
        this.setUserOS();
        this.setUserAgent();
    }

    /** 
     * @function setUserOS
     * @description SET User Operating System eg: mac,windows,etc
     * @returns {string} - Appends OSName to Pages.$body
     */
     private setUserOS = () => {
        var OSName = "";
        if (navigator.appVersion.indexOf("Win") != -1) OSName = "windows";
        if (navigator.appVersion.indexOf("Mac") != -1) OSName = "mac";
        if (navigator.appVersion.indexOf("X11") != -1) OSName = "unix";
        if (navigator.appVersion.indexOf("Linux") != -1) OSName = "linux";

        this.addClass(this.body, OSName);
    }

    /** 
     * @function setUserAgent
     * @description SET User Device Name to mobile | desktop
     * @returns {string} - Appends Device to Pages.$body
     */
    private setUserAgent = () => {
        if (navigator.userAgent.match(/Android|BlackBerry|iPhone|iPad|iPod|Opera Mini|IEMobile/i)) {
            this.addClass(this.body, 'mobile');
        } else {
            this.addClass(this.body, 'desktop');
            if (navigator.userAgent.match(/MSIE 9.0/)) {
                this.addClass(this.body, 'ie9');
            }
        }
    };

    public isVisible(element): boolean {
        return (element.offsetWidth > 0 || element.offsetHeight > 0)
    }

    /** 
     * @function isVisibleXs
     * @description Checks if the screen size is XS - Extra Small i.e below W480px
     * @returns boolean
     */
    public isVisibleXs(): boolean {
        let $pg_el = document.getElementById('pg-visible-xs');
        
        if (!$pg_el) {
            const $util_el = document.createElement('div');
            $util_el.className = "visible-xs";
            $util_el.setAttribute("id", "pg-visible-xs");
            this.body.appendChild($util_el)
            $pg_el = document.getElementById('pg-visible-xs');
        }

        return ($pg_el.offsetWidth === 0 && $pg_el.offsetHeight === 0) ? false : true;
    }

    /** 
     * @function isVisibleSm
     * @description Checks if the screen size is SM - Small Screen i.e Above W480px
     * @returns boolean
     */
    public isVisibleSm(): boolean {
        let $pg_el = document.getElementById('pg-visible-sm');
        
        if(!$pg_el) {
            const $util_el = document.createElement('div');
            $util_el.className = "visible-sm";
            $util_el.setAttribute("id", "pg-visible-sm");
            this.body.appendChild($util_el)
            $pg_el = document.getElementById('pg-visible-sm');
        }

        return ($pg_el.offsetWidth === 0 && $pg_el.offsetHeight === 0) ? false : true;
    }

    /** 
     * @function isVisibleMd
     * @description Checks if the screen size is MD - Medium Screen i.e Above W1024px
     * @returns boolean
     */
    public isVisibleMd(): boolean {
        let $pg_el = document.getElementById('pg-visible-md');
        
        if (!$pg_el){
            const $util_el = document.createElement('div');
            $util_el.className = "visible-md";
            $util_el.setAttribute("id", "pg-visible-sm");
            this.body.appendChild($util_el)
            $pg_el = document.getElementById('pg-visible-md');
        }

        return ($pg_el.offsetWidth === 0 && $pg_el.offsetHeight === 0) ? false : true;
    };

    /** 
     * @function isVisibleLg
     * @description Checks if the screen size is LG - Large Screen i.e Above W1200px
     * @returns boolean
     */
    public isVisibleLg(): boolean {
        let $pg_el = document.getElementById('pg-visible-lg');

        if(!$pg_el){
            const $util_el = document.createElement('div');
            $util_el.className = "visible-lg";
            $util_el.setAttribute("id", "pg-visible-lg");
            this.body.appendChild($util_el)
            $pg_el = document.getElementById('pg-visible-lg');
        }

        return ($pg_el.offsetWidth === 0 && $pg_el.offsetHeight === 0) ? false : true;
    };

    // class manipulation, since 2.0.0 requires polyfill.js
    public hasClass (el, className) {
        return el.classList ? el.classList.contains(className) : new RegExp('\\b'+ className+'\\b').test(el.className);
    }

    public addClass(el: HTMLElement, className: string) {
        if (el.classList) {
            el.classList.add(className);
        } else if (!this.hasClass(el, className)) {
            el.className += ' ' + className;
        }
    }

    public removeClass(el: HTMLElement, className: string) {
        if (el.classList) {
            el.classList.remove(className);
        } else {
            el.className = el.className.replace(new RegExp('\\b'+ className+'\\b', 'g'), '');
        }
    }

    public toggleClass(el: HTMLElement, className: string) {
        if (this.hasClass(el, className)){
            this.removeClass(el, className)
        } else {
            this.addClass(el, className)
        }
    }

    public isTouchDevice() {
        return 'ontouchstart' in document.documentElement;
    }

    // selection methods
    public getElementsByClassName(element: Element, classNAME: string): Element[] { // returns Array
        return [].slice.call(element[getElementsByCLASSNAME](classNAME));
    }

    public queryElement(selector, parent?: Element|Document): HTMLElement {
        var lookUp = parent ? parent : document;
        return typeof selector === 'object' ? selector : lookUp.querySelector(selector);
    }
    
    public getClosest(element: Node, selector) { //element is the element and selector is for the closest parent element to find
        // source http://gomakethings.com/climbing-up-and-down-the-dom-tree-with-vanilla-javascript/
        var firstChar = selector.charAt(0);
        for ( ; element && element !== document; element = element[parentNode] ) { // Get closest match
            if (firstChar === '.') {// If selector is a class
                if (this.queryElement(selector, <Element>element[parentNode]) !== null && this.hasClass(element, selector.replace('.',''))) { 
                    return element; 
                }
            } else if (firstChar === '#') { // If selector is an ID
                if ((<Element>element).id === selector.substr(1)) { 
                    return element; 
                }
            }
        }

        return false;
    }

    public addEvent(el, type, handler) {
        if (el.attachEvent) {
            el.attachEvent('on' + type, handler); 
        } else {
            el.addEventListener(type, handler);
        }
    }

    public removeEvent(el, type, handler) {
        if (el.detachEvent) {
            el.detachEvent('on'+type, handler); 
        } else {
            el.removeEventListener(type, handler);
        }
    };

    // event attach jQuery style / trigger  since 1.2.0
    public on(element, event, handler) {
        element.addEventListener(event, handler, false);
    }

    public off(element, event, handler) {
        element.removeEventListener(event, handler, false);
    }

    public one(element, event, handler) { // one since 2.0.4
        const self = this;
        self.on(element, event, function handlerWrapper(e) {
            handler(e);
            self.off(element, event, handlerWrapper);
        });
    }

    public live(selector, event, callback, context?) {
        this.addEvent(context || document, event, function(e) {
            var found, el = e.target || e.srcElement;
            while (el && el.matches && el !== context && !(found = el.matches(selector))) {
                el = el.parentElement;
            }

            if (found) {
                callback.call(el, e);
            }
        });
    };

    public extend<A>(a: A, b: A): A {
        for (var key in b) {
            if (b.hasOwnProperty(key)) {
                a[key] = b[key];
            }
        }

        return a;
    }
}

export var pg = new Pages();


